import { supabase } from './supabase'
import { Vaga, VagaFormData, VagaFilter } from '../types/database'

// Funo para buscar todas as vagas
export async function getVagas(filter?: VagaFilter): Promise<Vaga[]> {
  try {
    let query = supabase
      .from('vagas')
      .select('*')
      .order('created_at', { ascending: false })

    // Aplicar filtros se fornecidos
    if (filter?.cliente) {
      query = query.eq('cliente', filter.cliente)
    }
    if (filter?.site) {
      query = query.eq('site', filter.site)
    }
    if (filter?.categoria) {
      query = query.eq('categoria', filter.categoria)
    }
    if (filter?.cargo) {
      query = query.eq('cargo', filter.cargo)
    }
    if (filter?.celula) {
      query = query.eq('celula', filter.celula)
    }

    const { data: vagas, error } = await query

    if (error) {
      throw new Error(error.message)
    }

    return vagas || []
  } catch (error) {
    console.error('Erro ao buscar vagas:', error)
    return []
  }
}

// Funo para buscar uma vaga por ID
export async function getVagaById(id: string): Promise<Vaga | null> {
  try {
    const { data: vaga, error } = await supabase
      .from('vagas')
      .select('*')
      .eq('id', id)
      .single()

    if (error) {
      throw new Error(error.message)
    }

    return vaga
  } catch (error) {
    console.error('Erro ao buscar vaga:', error)
    return null
  }
}

// Funo para criar uma nova vaga
export async function createVaga(vagaData: VagaFormData, userId: string): Promise<Vaga | null> {
  try {
    console.log(' [createVaga] Iniciando criao de vaga')
    console.log(' [createVaga] Dados recebidos:', vagaData)
    console.log(' [createVaga] User ID:', userId)

    // Validar dados obrigatrios
    const requiredFields = ['site', 'categoria', 'cargo', 'cliente', 'celula']
    const missingFields = requiredFields.filter(field => !vagaData[field as keyof VagaFormData]?.trim())
    
    if (missingFields.length > 0) {
      throw new Error(`Campos obrigatrios no preenchidos: ${missingFields.join(', ')}`)
    }

    const insertData = {
      ...vagaData,
      created_by: userId,
      updated_by: userId
    }

    console.log(' [createVaga] Dados para insero:', insertData)
    console.log(' [createVaga] Iniciando insero no Supabase...')

    const startTime = Date.now()
    
    const { data: vaga, error } = await supabase
      .from('vagas')
      .insert(insertData)
      .select()
      .single()

    const endTime = Date.now()
    console.log(` [createVaga] Operao concluda em ${endTime - startTime}ms`)

    if (error) {
      console.error(' [createVaga] Erro do Supabase:', error)
      console.error(' [createVaga] Cdigo do erro:', error.code)
      console.error(' [createVaga] Detalhes do erro:', error.details)
      throw new Error(`Erro do banco de dados: ${error.message}`)
    }

    console.log(' [createVaga] Vaga criada com sucesso:', vaga)

    // Disparar evento de atualizao
    window.dispatchEvent(new CustomEvent('vaga-created', { detail: vaga }))

    return vaga
  } catch (error) {
    console.error(' [createVaga] Erro geral:', error)
    throw error // Re-throw para que o componente possa tratar
  }
}

// Funo para atualizar lista de vagas
export async function refreshVagasList(): Promise<Vaga[]> {
  try {
    const { data, error } = await supabase
      .from('vagas')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) {
      throw new Error(error.message)
    }

    // Disparar evento de atualizao
    window.dispatchEvent(new CustomEvent('vagas-updated', { detail: data || [] }))

    return data || []
  } catch (error) {
    console.error('Erro ao atualizar vagas:', error)
    return []
  }
}

// Funo para atualizar uma vaga
export async function updateVaga(id: string, vagaData: Partial<VagaFormData>, userId: string): Promise<Vaga | null> {
  try {
    const { data: vaga, error } = await supabase
      .from('vagas')
      .update({
        ...vagaData,
        updated_by: userId
      })
      .eq('id', id)
      .select()
      .single()

    if (error) {
      throw new Error(error.message)
    }

    return vaga
  } catch (error) {
    console.error('Erro ao atualizar vaga:', error)
    return null
  }
}

// Funo para excluir uma vaga
export async function deleteVaga(id: string): Promise<boolean> {
  try {
    const { error } = await supabase
      .from('vagas')
      .delete()
      .eq('id', id)

    if (error) {
      throw new Error(error.message)
    }

    return true
  } catch (error) {
    console.error('Erro ao excluir vaga:', error)
    return false
  }
}

// Funo para buscar clientes nicos
export async function getClientes(): Promise<string[]> {
  try {
    const { data, error } = await supabase
      .from('vagas')
      .select('cliente')
      .order('cliente')

    if (error) {
      throw new Error(error.message)
    }

    // Remover duplicatas e retornar array de strings
    const clientes = [...new Set(data?.map((item: any) => item.cliente) || [])] as string[]
    return clientes
  } catch (error) {
    console.error('Erro ao buscar clientes:', error)
    return []
  }
}

